body {
  background-color: azure;
}

.breed-select {
  width: 300px;
  border-radius: 8px;
  margin: 24px auto;
  padding: 10px;
}

.breed-name {
  text-align: left;
  margin-top: 0;
}

.breed-img {
  display: block;
}

.cat-info-box {
  width: 80vw;
  flex-direction: row;
  gap: 24px;
  margin-left: auto;
  margin-right: auto;
  display: flex;
}

.visually-hidden {
  white-space: nowrap;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
  -webkit-clip-path: inset(50%);
  clip-path: inset(50%);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.loader {
  width: 48px;
  height: 48px;
  background: azure;
  border-radius: 50%;
  animation: 1s ease-in-out infinite alternate roll;
  position: relative;
}

.loader:after {
  content: "";
  border: 5px solid #ff3d00;
  border-color: #ff3d00 #0000;
  border-radius: 50%;
  position: absolute;
  inset: 5px;
}

@keyframes roll {
  0% {
    transform: translateX(-150%)rotate(0);
  }

  100% {
    transform: translateX(150%)rotate(360deg);
  }
}

